
import Swal from 'sweetalert2';


export const swal = () => {


    return (

        Swal.fire({
            title: 'Credenciales invlidas',
            text: 'Por favor, verific tus datos',
            confirmButtonColor: '#ff834f',
            confirmButtonText: 'Aceptar',
            timerProgressBar: true,
            width: '400px',
        })
    )
}
    
    